
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'user',
props:['loginname'],
data:function(){
return{
userinfo:'',
collect_topics:''
}
},
components:{
Card,
Group,
Cell
},
beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/user/'+to.params.loginname).then(function(resopnse){
console.log(resopnse.data.data)
vm.userinfo = resopnse.data.data
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(resopnse){
vm.collect_topics = resopnse.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var app = this;
this.$http.get('/user/'+to.params.loginname).then(function(resopnse){
console.log(resopnse.data.data)
app.userinfo = resopnse.data.data
});
this.$http.get('/topic_collect/'+to.params.loginname).then(function(resopnse){
vm.collect_topics = resopnse.data.data
});
next();
}
}



