
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState} from 'vuex';
export default {
name: "topiclist",
props: ["name"],
computed:{
...mapState(['topics'])
},
components: {
Group,
Cell,
Scroller,
Tab,
TabItem

},
data:function() {
return {
lists: [],
page:1,//
limit:10,//

pullupConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...',
},//
pulldownConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...',
}
};
},
methods:{

getDescByTab:function(tab){
return this.$store.getters.getTopicDescByTab(tab)
},
routeto:function(index){



this.$router.push('/topiclist/'+this.topics[index].name);
},

loadMoreData:function(){
var app = this;

this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page+1,
limit:this.limit
}
}).then(function(response){



for(var i =0;i<response.data.data.length;i++){
app.lists.push(response.data.data[i])
}
})


app.$refs.pullup.donePullup();
},

freshDate:function(){
var app = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
app.lists = response.data.data
app.$refs.pullup.donePulldown();
}
})
}

},



beforeRouteEnter: function(to, from, next) {




next(function(vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page:vm.page,
limit:vm.limit
}
})
.then(function(response) {


if (response.data.success) {
vm.lists = response.data.data;
}
});
});
},

beforeRouteUpdate: function(to, from, next) {
var app = this;

this.$http
.get("/topics", {
params: {
tab: to.params.name,
page:this.page,
limit:this.limit
}
})
.then(function(response) {

if (response.data.success) {
app.lists = response.data.data;
}
});

next();
}
};



