export default {
    name:'center',
    //
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //
        //this.$router.pushhistory
        //
        this.$router.replace('/user/'+this.loginname)

    }
}



